import axios from 'axios'
const baseUrl = process.env.BASE_UEL
export const getList = (page) => {
    
   return axios({
       method:'post',
       params:page,
       url:baseUrl+'/find'
   }).then((res) => {
        if(res.status){
            return res.data.content
        }
   })

}
export const getCount = () => {
    
   return axios({
       method:'post',
       url:baseUrl+'/count'
   }).then((res) => {
        if(res.status){
            return res.data
        }
   })

}
export const delUser = (userId) => {
    
   return axios({
       method:'post',
       params:{
            memberId:userId
       },
       url:baseUrl+'/del'
   }).then((res) => {
       console.log(res)
        if(res.status){
            return true
        }
            return false
        
        
   })

}


