import axios from 'axios'
const baseUrl = process.env.BASE_UEL

export default {
     registerUser({ commit }, userInfo) {
        console.log(baseUrl)
         
        // axios.post('/regin',{memberName:"456",memberPwd:"123",memberLvl:2}).then((res) => {
         return axios({
                method:"post",
                params:userInfo,
                url:baseUrl+'/regin'
            }).then((res)=>{

        
            console.log(res)
            commit('REMEBER_USER', userInfo)
            return true
        }).catch(function (err) {

            console.log(err)
            return false

        })
    },
    checkUser({ commit }, userInfo) {
        
        return axios({
            method:'post',
            params:userInfo,
            url:baseUrl+'/login'
        }).then((res) => {
          
           if(res.status == 200){

                commit('REMEBER_USER', userInfo)
                return true
            }
            return false
            
        })
    }
   
}


