import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/login'
import Hello from '@/components/Hello'
import Register from '@/components/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'register',
      component: Register
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/Hello',
      name: 'Hello',
      component: Hello
    }
  ]
})



